import { Component, OnInit } from '@angular/core';
import {Observable} from 'rxjs';
import {DataServiceService} from '../data-service.service';
import {Team} from '../team';
import {Game} from '../game';


@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {

  teams:Team[];
  games1:Game[];
  games2:Game[];
  games3:Game[];
  show1 = true;
  show2 = true;
  show3 = true;

  constructor(private dataService: DataServiceService) { }

  ngOnInit() {
    this.getAFLTeams();
    this.getGames2();
    this.getGames1();
    this.getGames3();
    
  }
  getAFLTeams(): void {
    this.dataService.getTeams().subscribe(temp => { this.teams = temp;});
  }
  
  getGames2(): void {
    this.dataService.getGames2().subscribe(temp => { this.games2 = temp;});
  }
  getGames1(): void {
    this.dataService.getGames1().subscribe(temp => { this.games1 = temp;});
  }
  getGames3(): void {
    this.dataService.getGames3().subscribe(temp => { this.games3 = temp;});
  }


 
  
}
